import logo_desk from "../../assets/images/logo_desktop.svg";
import styled from "styled-components";
import Select from "../../assets/UI/Select";
import { A } from "../../assets/styles/component";
import { EngLoc } from "../../assets/constants/localizations";

const StyledHeader = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 111px;

  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  font-weight: 400;
  font-size: 18px;
  z-index: 999;

  transition: 0.25s;

  ${({ scrollY }) =>
    scrollY <= 100
      ? ""
      : `
    background: rgba(71, 8, 204, 0.65);
    height: 78px;`}
`;

const Nav = styled.nav`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 15%);
  margin: 0 15%;

  @media (max-width: 1200px) {
    width: calc(100vw - 10%);
    margin: 0 10%;
  }
`;

const NavTabs = styled.div`
  display: flex;
  gap: 35px;
`;

const Logo = styled.img`
  width: 128px;
  height: 62px;
`;

const Header = ({ navItem, language, scrollY, changeLng}) => {
  const changeLocalization = (lang = "Ru") => {
    localStorage.setItem("Language", lang);
    changeLng(lang);
  };

  return (
    <StyledHeader scrollY={scrollY}>
      <Nav>
        <Logo loading="lazy" src={logo_desk} alt="За стеклом" />
        <NavTabs>
          {navItem.map((item) => (
            <A
              href={item.href}
              size="18"
              key={Math.random()}
              target={item.blank && "_blank"}
            >
              {item.name}
            </A>
          ))}
        </NavTabs>
        <div style={{ display: "flex", gap: 15 + "px" }}>
          <Select
            type="languages"
            name={language === "Ru" ? "Ru" : "Eng"}
            paddingX="12"
            color="#1073D3"
            bg_color="#E7F5FF"
            onChange={changeLocalization}
          ></Select>
          <Select
            type="products"
            name={language === "Ru" ? "Войти" : EngLoc.buttons.login}
          ></Select>
        </div>
      </Nav>
    </StyledHeader>
  );
};

export default Header;
